import React, { useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import TableComponent from "./TableComponent";
import pageStyles from "./index.module.scss";
const CartPage = () => {
  const { register, handleSubmit } = useForm();
  // setMethod
  const [method] = useState("cridetCard");
  const submitHandler = async (data) => {
    console.log(data);
  };
  return (
    <div>
      <Container>
        <Row style={{ justifyContent: "center" }}>
          <Col xs={12}>
            <TableComponent />
          </Col>
          <Col md={12} className={pageStyles.formContainer}>
            <form onSubmit={handleSubmit(submitHandler)}>
              <Row className={pageStyles.customerInfoBody}>
                {/* <Col xs={12} lg={10} className={pageStyles.form}>
                  <div className={`${pageStyles.selectInput}`}>
                    <label htmlFor="currentCountry">طريقة الدفع</label>
                    <select
                      name=""
                      id=""
                      onChange={(e) => setMethod(e.target.value)}
                    >
                      <option value={null}> يرجي الاختيار</option>
                      <option value="cash">كاش</option>
                      <option value="cridetCard">
                        بطاقة بنكية (credit card )
                      </option>
                    </select>
                  </div>
                </Col> */}
                {method === "cash" && (
                  <Col
                    xs={12}
                    lg={10}
                    className={pageStyles.customerInfoContainer}
                  >
                    <div className={`${pageStyles.formInput}  `}>
                      <label htmlFor="fullName"> الاسم بالكامل </label>
                      <input
                        name="fullName"
                        id="fullName"
                        {...register("fullName")}
                      />
                    </div>
                    <div className={`${pageStyles.formInput}  `}>
                      <label htmlFor="phone">رقم التليفون</label>
                      <input name="phone" id="phone" {...register("phone")} />
                    </div>
                    <div className={`${pageStyles.formInput}  `}>
                      <label htmlFor="email">الإيميل</label>
                      <input
                        type={"email"}
                        name="email"
                        id="email"
                        {...register("email")}
                      />
                    </div>
                    <div className={`${pageStyles.formInput}  `}>
                      <label htmlFor="address">العنوان بالتفصيل</label>
                      <input
                        name="address"
                        id="address"
                        {...register("address")}
                      />
                    </div>
                    <div className={pageStyles.submiteBtn}>
                      <button type="submit">
                        <svg
                          width="35"
                          height="35"
                          viewBox="0 0 1024 1024"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path d="M512 64 128 192v384c0 212.1 171.9 384 384 384s384-171.9 384-384V192L512 64zm312 512c0 172.3-139.7 312-312 312S200 748.3 200 576V246l312-110 312 110v330z" />
                          <path d="M378.4 475.1a35.91 35.91 0 0 0-50.9 0 35.91 35.91 0 0 0 0 50.9l129.4 129.4 2.1 2.1a33.98 33.98 0 0 0 48.1 0L730.6 434a33.98 33.98 0 0 0 0-48.1l-2.8-2.8a33.98 33.98 0 0 0-48.1 0L483 579.7 378.4 475.1z" />
                        </svg>
                        حفظ ومتابعة
                      </button>
                    </div>
                  </Col>
                )}
                {method === "cridetCard" && (
                  <Col
                    xs={12}
                    lg={10}
                    className={pageStyles.customerInfoContainer}
                  >
                    <div className={`${pageStyles.formInput}  `}>
                      <label htmlFor="fullName"> الاسم صاحب البطاقة </label>
                      <input
                        name="cardOwnerName"
                        id="cardOwnerName"
                        {...register("cardOwnerName", { required: true })}
                      />
                    </div>
                    <div className={`${pageStyles.formInput}  `}>
                      <label htmlFor="fullName">بطاقة الائتمان</label>
                      <Row>
                        {/* TODO Complete styleing this section */}
                        <Col xs={6}>
                          <input
                            name="cardNumber"
                            id="cardNumber"
                            placeholder={"رقم البطاقة"}
                            type={"number"}
                            {...register("cardNumber", {
                              required: true,
                              // eslint-disable-next-line no-useless-escape
                              pattern: `(^4[0-9]{12}(?:[0-9]{3})?$)|(^(?:5[1-5][0-9]{2}|222[1-9]|22[3-9][0-9]|2[3-6][0-9]{2}|27[01][0-9]|2720)[0-9]{12}$)|(3[47][0-9]{13})|(^3(?:0[0-5]|[68][0-9])[0-9]{11}$)|(^6(?:011|5[0-9]{2})[0-9]{12}$)|(^(?:2131|1800|35\d{3})\d{11}$)`,
                            })}
                          />
                        </Col>
                        <Col xs={4} className={pageStyles.colInput}>
                          <input
                            name="expiryDate"
                            id="expiryDate"
                            placeholder={"سنة / شهر"}
                            type={"text"}
                            {...register("expiryDate", {
                              required: true,
                              // eslint-disable-next-line no-useless-escape
                              pattern: ` /^(0[1-9]|1[0-2])\/?([0-9]{4}|[0-9]{2})$/`,
                            })}
                          />
                        </Col>
                        <Col xs={2} className={pageStyles.colInput}>
                          <input
                            name="CVV"
                            id="CVV"
                            placeholder={"CVV"}
                            type={"number"}
                            {...register("CVV", {
                              required: true,
                            })}
                          />
                        </Col>
                      </Row>
                    </div>
                    <div className={pageStyles.submiteBtn}>
                      <button type="submit">
                        <svg
                          width="35"
                          height="35"
                          viewBox="0 0 1024 1024"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path d="M512 64 128 192v384c0 212.1 171.9 384 384 384s384-171.9 384-384V192L512 64zm312 512c0 172.3-139.7 312-312 312S200 748.3 200 576V246l312-110 312 110v330z" />
                          <path d="M378.4 475.1a35.91 35.91 0 0 0-50.9 0 35.91 35.91 0 0 0 0 50.9l129.4 129.4 2.1 2.1a33.98 33.98 0 0 0 48.1 0L730.6 434a33.98 33.98 0 0 0 0-48.1l-2.8-2.8a33.98 33.98 0 0 0-48.1 0L483 579.7 378.4 475.1z" />
                        </svg>
                        حفظ ومتابعة
                      </button>
                    </div>
                  </Col>
                )}
              </Row>
            </form>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default CartPage;
